"use client";

import { useEffect, useMemo, useState } from "react";
import { loadWods } from "@/lib/wodStore";
import {
  useWodComments,
  type Comment,
} from "@/components/athlete/WodCommentsStore";
import { Modal } from "@/components/ui/modal";
import { useModal } from "@/hooks/useModal";
import type { Wod } from "@/lib/mockWod";

function formatTs(ts: number) {
  return new Date(ts).toLocaleString("es-ES", {
    day: "2-digit",
    month: "2-digit",
    hour: "2-digit",
    minute: "2-digit",
  });
}

export default function AdminWodCommentsPage() {
  const { commentsByDate, deleteComment } = useWodComments();
  const { isOpen, openModal, closeModal } = useModal();

  // ✅ Importante: NO leer localStorage en render (evita hydration mismatch)
  const [wods, setWods] = useState<Wod[]>([]);
  const [selectedDate, setSelectedDate] = useState<string>("");
  const [pending, setPending] = useState<{ date: string; c: Comment } | null>(
    null
  );

  useEffect(() => {
    setWods(loadWods());
  }, []);

  const dates = useMemo(() => {
    const fromWods = wods.map((w) => w.date);
    const fromComments = Object.keys(commentsByDate);
    return Array.from(new Set([...fromWods, ...fromComments]))
      .sort()
      .reverse();
  }, [wods, commentsByDate]);

  // ✅ Auto-selección cuando ya hay fechas disponibles
  useEffect(() => {
    if (!selectedDate && dates.length > 0) setSelectedDate(dates[0]);
  }, [dates, selectedDate]);

  const list = selectedDate ? commentsByDate[selectedDate] ?? [] : [];

  const askDelete = (date: string, c: Comment) => {
    setPending({ date, c });
    openModal();
  };

  const confirmDelete = () => {
    if (!pending) return;
    deleteComment(pending.date, pending.c.id);
    closeModal();
    setPending(null);
  };

  return (
    <div className="max-w-5xl">
      <div className="flex items-start justify-between gap-4">
        <div>
          <h1 className="text-xl font-semibold">Comentarios (WOD)</h1>
          <p className="mt-1 text-sm text-gray-500">
            Moderá comentarios por fecha de WOD.
          </p>
        </div>
      </div>

      <div className="mt-4 grid gap-4 lg:grid-cols-3">
        {/* FECHAS */}
        <div className="rounded-2xl border border-gray-200 bg-white p-4 shadow-sm">
          <div className="text-sm font-semibold">Fechas</div>
          <div className="mt-3 space-y-2">
            {dates.length === 0 && (
              <div className="text-sm text-gray-500">
                Todavía no hay comentarios.
              </div>
            )}

            {dates.map((d) => {
              const count = (commentsByDate[d] ?? []).length;
              const active = d === selectedDate;
              return (
                <button
                  key={d}
                  onClick={() => setSelectedDate(d)}
                  className={`w-full rounded-xl border p-3 text-left ${
                    active
                      ? "border-gray-900 bg-gray-50"
                      : "border-gray-200 hover:bg-gray-50"
                  }`}
                >
                  <div className="text-sm font-medium">{d}</div>
                  <div className="mt-1 text-xs text-gray-500">
                    {count} comentario(s)
                  </div>
                </button>
              );
            })}
          </div>
        </div>

        {/* LISTA */}
        <div className="lg:col-span-2 rounded-2xl border border-gray-200 bg-white p-4 shadow-sm">
          <div className="flex items-center justify-between">
            <div className="text-sm font-semibold">
              {selectedDate ? `WOD ${selectedDate}` : "Seleccioná una fecha"}
            </div>
            <div className="text-xs text-gray-500">{list.length} total</div>
          </div>

          <div className="mt-3 space-y-2">
            {selectedDate && list.length === 0 && (
              <div className="text-sm text-gray-500">
                No hay comentarios para esta fecha.
              </div>
            )}

            {list.map((c) => (
              <div key={c.id} className="rounded-xl border border-gray-200 p-3">
                <div className="flex items-start justify-between gap-3">
                  <div className="text-sm text-gray-800">{c.text}</div>
                  <button
                    onClick={() => askDelete(selectedDate, c)}
                    className="rounded-lg border border-red-300 bg-white px-3 py-1 text-xs text-red-700"
                  >
                    Borrar
                  </button>
                </div>
                <div className="mt-2 text-xs text-gray-500">
                  {formatTs(c.createdAt)}
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>

      {/* CONFIRM MODAL */}
      <Modal
        isOpen={isOpen}
        onClose={closeModal}
        className="max-w-[420px] p-5 lg:p-6"
      >
        <div className="text-center">
          <h4 className="mb-2 text-xl font-semibold text-gray-800 dark:text-white/90">
            Borrar comentario
          </h4>

          <p className="text-sm leading-6 text-gray-500 dark:text-gray-400">
            ¿Seguro que querés borrar este comentario?
          </p>

          <div className="mt-6 flex items-center justify-center gap-3">
            <button
              type="button"
              onClick={closeModal}
              className="rounded-lg border border-gray-300 bg-white px-4 py-2 text-sm font-medium text-gray-700 hover:bg-gray-50"
            >
              Cancelar
            </button>

            <button
              type="button"
              onClick={confirmDelete}
              className="rounded-lg bg-error-500 px-4 py-2 text-sm font-medium text-white shadow-theme-xs hover:bg-error-600"
            >
              Sí, borrar
            </button>
          </div>
        </div>
      </Modal>
    </div>
  );
}
