import type { Wod } from "./mockWod";

const STORAGE_KEY = "a615.wods";

export function loadWods(): Wod[] {
  if (typeof window === "undefined") return [];
  const raw = localStorage.getItem(STORAGE_KEY);
  return raw ? JSON.parse(raw) : [];
}

export function saveWods(wods: Wod[]) {
  localStorage.setItem(STORAGE_KEY, JSON.stringify(wods));
}

export function getWodByDate(date: string): Wod | undefined {
  return loadWods().find((w) => w.date === date);
}

export function upsertWod(wod: Wod) {
  const wods = loadWods();
  const idx = wods.findIndex((w) => w.date === wod.date);

  if (idx >= 0) wods[idx] = wod;
  else wods.unshift(wod);

  saveWods(wods);
}

export function deleteWod(date: string) {
  const wods = loadWods().filter((w) => w.date !== date);
  saveWods(wods);
}
